import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useAdmissionOngoingApplicationAllottmentRevert } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";
import AdmissionAllottmentRevertSelectStudent from "./AdmissionAllottmentRevertSelectStudent";
const AdmissionAllottmentRevertConfirm = ({ onClose, appId, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [step, setStep] = useState(1);
  const [studentId, setStudentId] = useState([]);
  const [state, setState] = useState({
    query: "",
  });
  const [admissionOngoingApplicationAllottmentRevert] =
    useAdmissionOngoingApplicationAllottmentRevert();

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onRevertBack = () => {
    let validation = requiredField(state);
    if (existingRequiredField(validation)) {
      if (state.query === "Confirm" && appId) {
        setDisabled((pre) => !pre);
        admissionOngoingApplicationAllottmentRevert({
          appId: appId,
          bodyParameter: {
            student_list: studentId,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };
  const onSelectStudent = (val) => {
    if (studentId?.includes(val?._id)) {
      setStudentId((prev) => prev?.filter((dt) => dt !== val?._id));
    } else {
      setStudentId((prev) => [...prev, val?._id]);
    }
  };

  const onAddStudent = () => {
    setStep("");
  };

  return (
    <>
      {step === 1 ? (
        <AdmissionAllottmentRevertSelectStudent
          title={t("select_student")}
          onClose={onClose}
          appId={appId}
          isMultiSelect
          onMultiSelect={onSelectStudent}
          onDone={onAddStudent}
          selectList={studentId}
          buttonTitle="select"
        />
      ) : (
        <Modal onClose={() => setStep(1)}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("are_your_sure_to_revert_back_allottment")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => setStep(1)}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("type_confirm_to_continue")}
                placeholder={t("type_confirm_to_continue")}
                name="query"
                value={state.query}
                type="text"
                onChange={onInputChange}
                errorShow={errorField["query"]}
              />
              <CreateButton label="confirm" onAction={onRevertBack} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionAllottmentRevertConfirm;
