import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import style from "../../../PlateformUser/PlateformUser.module.css";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../../Ui/Button/CreateButton";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import AdmissionAllottmentRevertSelectStudentCard from "./AdmissionAllottmentRevertSelectStudentCard";
import { useAdmissionOngoingApplicationAllottment } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
const AdmissionAllottmentRevertSelectStudent = ({
  title,
  onClose,
  selectedId,
  onSelectStudent,
  appId,
  isMultiSelect,
  onMultiSelect,
  selectList,
  onDone,
  buttonTitle,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionOngoingApplicationAllottment,
    admissionOngoingApplicationAllottmentLoading,
  } = useAdmissionOngoingApplicationAllottment({
    data: {
      appId: appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationAllottment?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationAllottment?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationAllottment?.list]);

  const onSearchEvent = (e) => {
    if (
      !admissionOngoingApplicationAllottmentLoading &&
      admissionOngoingApplicationAllottment?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onSelect = (value) => {
    if (selectedId === value?._id) {
    } else {
      onSelectStudent(value);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{title ?? t("student_list")}</h6>
              </div>
              <div className={style.modal_container_btn_container}>
                {isMultiSelect && (
                  <CreateButton
                    label={t(buttonTitle ?? "select")}
                    count={selectList?.length}
                    onAction={onDone}
                    customStyle={{
                      margin: "0",
                      padding: "0.3rem 0.5rem",
                    }}
                  />
                )}
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "1rem",
            }}
          >
            <div
              className={style.assign_search_container}
              style={{
                marginBottom: "1rem",
              }}
            >
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>

            {admissionOngoingApplicationAllottment?.list?.map(
              (applicant, index) =>
                admissionOngoingApplicationAllottment?.list?.length ===
                index + 1 ? (
                  <div key={applicant?._id} ref={ref}>
                    <AdmissionAllottmentRevertSelectStudentCard
                      applicant={applicant}
                      applicantId={selectedId}
                      onSelect={onSelect}
                      selectList={selectList}
                      isMultiSelect={isMultiSelect}
                      onMultiSelect={onMultiSelect}
                    />
                  </div>
                ) : (
                  <AdmissionAllottmentRevertSelectStudentCard
                    applicant={applicant}
                    key={applicant?._id}
                    applicantId={selectedId}
                    onSelect={onSelect}
                    selectList={selectList}
                    isMultiSelect={isMultiSelect}
                    onMultiSelect={onMultiSelect}
                  />
                )
            )}
            {admissionOngoingApplicationAllottmentLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
              )
            ) : search ? (
              admissionOngoingApplicationAllottment?.list?.length === 0 && (
                <EmptyMessage title="No students found related this search." />
              )
            ) : (
              admissionOngoingApplicationAllottment?.list?.length === 0 && (
                <EmptyMessage title="No students." />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default AdmissionAllottmentRevertSelectStudent;
